  //map
  onMarkerDragEnd = (coord, index) => {
    const { latLng } = coord;
    const lat = latLng.lat();
    const lng = latLng.lng();

    this.setState((prevState) => {
      const markers = [...this.state.markers];
      markers[index] = { ...markers[index], position: { lat, lng } };
      return { markers };
    });
  };

  //valiadtion
  //email validation
  isValidEmail = (email) => {
    var re =
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  };

  //phone validation
  isValidPhone = (phone) => {
    var re = /^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/;
    return re.test(phone);
  };

  businessContactType = (id) => {
    if (id === 0) {
      this.setState({ ABusinessType: "Sole Proprietorship" });
    } else if (id === 1) {
      this.setState({ ABusinessType: "Partnership" });
    } else if (id === 2) {
      this.setState({ ABusinessType: "Corporation" });
    } else if (id === 3) {
      this.setState({ ABusinessType: "Other" });
    }
    this.setState({ ABusinessTypeError: "" });
  };

  handleChange = (input) => (e) => {
    // console.log(input);
    // console.log(e.target.value);
    this.setState({ [input]: e.target.value });
    this.setState({ [input + "Error"]: "" });
    if (input === "confirmPassword") {
      this.setState({ passwordError: "" });
    }

    if (input === "AState" && e.target.value !== "select") {
      this.fetchAreaList(e.target.value);
    }

    // document.getElementById("reg_error").style.display = "none";
  };

  fetchAreaList = (id) => {
    this.setState({ loading: true });
    var data = {
      StateId: id,
    };

    fetch(api.apiUrl + "get_areas", {
      method: "POST",
      // headers: authHeader(),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((response) => response.json())
      .then((responseJsonFromServerData) => {
        this.setState({
          areaArr: responseJsonFromServerData,
          loading: false,
        });
      })
      .catch((error) => {
        console.error(error);
        this.setState({ loading: false });
      });
  };

  setupCheckboxes = (serverData) => {
    return serverData.map((el) => {
      return {
        serviceId: el.id,
        serviceName: el.name,
        serviceCode: el.code,
        isChecked: false,
      };
    });
  };

  service = (id) => {
    this.setState({ serviceError: "" });
    // console.log(id);
    this.setState({ loading: true });
    var data = {
      service_id: id,
    };

    // console.log(id);
    if (id === 0) {
      this.setState(
        {
          checkbox_garage: !this.state.checkbox_garage,
        },
        () => {
          //garage
          if (this.state.checkbox_garage) {
            fetch(api.apiUrl + "fetch_service", {
              method: "POST",
              // headers: authHeader(),
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(data),
            })
              .then((response) => response.json())
              .then((responseJsonFromServerData) => {
                // console.log(responseJsonFromServerData)
                this.setState({
                  garage_data: this.setupCheckboxes(responseJsonFromServerData),
                  loading: false,
                });
              })
              .catch((error) => {
                console.error(error);
                this.setState({ loading: false });
              });
          } else {
            this.setState({
              garage_data: [],
              loading: false,
            });
          }
        }
      );
    }

    if (id === 1) {
      this.setState(
        {
          checkbox_spares: !this.state.checkbox_spares,
        },
        () => {
          //spares
          if (this.state.checkbox_spares) {
            fetch(api.apiUrl + "fetch_service", {
              method: "POST",
              // headers: authHeader(),
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(data),
            })
              .then((response) => response.json())
              .then((responseJsonFromServerData) => {
                // console.log(responseJsonFromServerData);
                this.setState({
                  spares_data: this.setupCheckboxes(responseJsonFromServerData),
                  loading: false,
                });
              })
              .catch((error) => {
                console.error(error);
                this.setState({ loading: false });
              });
          } else {
            this.setState({
              spares_data: [],
              loading: false,
            });
          }
        }
      );
    }
    if (id === 2) {
      this.setState(
        {
          checkbox_ancillary: !this.state.checkbox_ancillary,
        },
        () => {
          //ancillary
          if (this.state.checkbox_ancillary) {
            fetch(api.apiUrl + "fetch_service", {
              method: "POST",
              // headers: authHeader(),
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(data),
            })
              .then((response) => response.json())
              .then((responseJsonFromServerData) => {
                // console.log(responseJsonFromServerData);
                this.setState({
                  ancillary_data: this.setupCheckboxes(
                    responseJsonFromServerData
                  ),
                  loading: false,
                });
              })
              .catch((error) => {
                console.error(error);
                this.setState({ loading: false });
              });
          } else {
            this.setState({
              ancillary_data: [],
              loading: false,
            });
          }
        }
      );
    }
  };

